*
{
    margin: 0;
    padding: 0;
}
.app 
{
    text-align: center;
    background-color: aqua;
    font-weight: bold;
    font-family: Gayatri;
    
}
.app h1
{
    margin-top: 5px;
}
.app input
{
    font-size: 13px;
    border-radius: 5px;
    border: 2px solid black;
    outline: none;
    padding: 8px 5px;
    width: 200px;
    margin-top: 20px;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
}
button 
{
    font-size: 13px;
    border-radius: 5px;
    border: 2px solid black;
    outline: none;
    padding: 8px 5px;
    width: 100px;
    margin-left: 10px;
    background-color: rgb(233, 233, 169);
    color: rgb(8, 8, 8);
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: bold;
}
nav{
    display: flex;
    justify-content: center;
    background: linear-gradient(122deg,#83eeea 0%, rgba(16,16,13,0.04) 40%, #a500ff 80%);
    margin-top: 10px;
    
}
nav ul{
    display: flex;
    justify-content: space-between;
    width: 40%;
    margin-top: 10px;
    align-items: center;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 8px; 
    font-family:Segoe UI Black ;
    transition: all 0.5s ease-in-out;
    
}
nav ul li{
    list-style-type: none;
    cursor: pointer;
}
nav ul li:hover{
    transform: scale(1.4);
}
.navActive
{
    color: chartreuse;
}
.app-container
{
  background: linear-gradient(90deg,#777ff2 0%, #ff00d0 80%); 
  font-size: 20px;
  font-weight: bold;
  margin-top: 20px;
  font-family: Comic Sans MS;
}